<template>
  <div class="browser">
    <cynav></cynav>
    <cymenu></cymenu>
    <div class="cy_example">
        <p class="cy_title">示例效果</p>
        <div class="line"></div>
        <div class="codeArea">

<codemirror ref="myEditor1" class="myEditor" :options="cmOptions"></codemirror>
<xmp ref="myEditorVal1" style="display:none">
&lt;p&gt;当前微信浏览环境：&lt;span id="demo1"&gt;&lt;/span&gt;&lt;/p&gt;
&lt;p&gt;当前环境支持webp格式：&lt;span id="demo2"&gt;&lt;/span&gt;&lt;/p&gt; 
&lt;script type="text/javascript"&gt;
  import browser from "cy_static/browser";
  var demo1 = document.querySelector("#demo1");
  var demo2 = document.querySelector("#demo2");
  demo1.innerHTML = browser.weixin ? "是" : "不是";
  demo2.innerHTML = browser.webp ? "是" : "不是";	
&lt;/script&gt;
</xmp >

        </div>
        <div class="submitCode">
            <input type="button" class="btn-submit" value="点击运行代码" @click="runcode()">
        </div>
        <div class="iframeBox">
            <iframe ref="iframe" src="#/runcode"></iframe>
        </div>
    </div>   
    <div class="cy_center">
      <h2 class="cy_h2">browser 标签页</h2>  
      <div class="codeModule">
          <h3 class="cy_h3">引入</h3> 
<codemirror ref="codeStyle1" class="codeStyle" :options="cmOptions1"></codemirror>
<xmp ref="codeStyleVal1" style="display:none">
&lt;script type="text/javascript"&gt;
    import browser from "cy_static/browser"; 
&lt;/script&gt;
</xmp >
 
      </div>

      <h2 class="cy_h2">代码演示</h2>
      <div class="codeModule">
<codemirror ref="codeStyle2" class="codeStyle" :options="cmOptions1"></codemirror>
<xmp ref="codeStyleVal2" style="display:none">
&lt;script type="text/javascript"&gt;
    var isWeixin = browser.weixin;
    var isWebp = browser.webp;
&lt;/script&gt;
</xmp >

      </div>

      <h2 class="cy_h2">API</h2>
      <div class="apiModule">
        <h2 class="cy_h2">对象属性</h2>
        <ul class="ul-props">
					<li>
						<p><a>jfmore</a> : <a>String</a></p>
						<p>畅由APP内嵌标识版本号；非常畅由APP返回""字符串；</p>
					</li>
					<li>
						<p><a>ios</a> : <a>Boolean</a></p>
						<p>在iphone|ipod|ipad|ios浏览器中返回 true；</p>
					</li>
					<li>
						<p><a>android</a> : <a>Boolean</a></p>
						<p>在android浏览器中返回 true；</p>
					</li>
					<li>
						<p><a>weixin</a> : <a>Boolean</a></p>
						<p>在微信micromessenger浏览器中返回 true；</p>
					</li>
					<li>
						<p><a>qq</a> : <a>Boolean</a></p>
						<p>在QQAPP中返回 true；</p>
					</li>
					<li>
						<p><a>qzone</a> : <a>Boolean</a></p>
						<p>在QQAPP、qzone中返回 true；</p>
					</li>
					<li>
						<p><a>webp</a> : <a>Boolean</a></p>
						<p>当前环境是否支持webp格式图片；</p>
					</li>
				</ul>
      </div>




    </div> 
  </div>
</template>

<script>
import {trim} from "../../../assets/js/codeStyle";
export default {
  name: 'browser',
  data () {
    return {
      msg: 'browser',
      cmOptions:{
            mode : "text/html",  // 模式
            indentUnit : 2,  // 缩进单位，默认2
            smartIndent : true,  // 是否智能缩进
            tabSize : 4,  // Tab缩进，默认4
            showCursorWhenSelecting : true

      },
      cmOptions1:{
            readOnly:"true",
            theme : "darcula",
            mode : "text/html",  // 模式
            indentUnit : 2,  // 缩进单位，默认2
            smartIndent : true,  // 是否智能缩进
            tabSize : 4,  // Tab缩进，默认4
            showCursorWhenSelecting : true
      }
    }
  },
  mounted(){
    var m = this;  
    ["myEditor", "codeStyle", "demoCode"].forEach((element)=>{
        var dom, valDom, index = 0, getDom = function() {
            index++;
            dom = m.$refs[element + index];
            valDom = m.$refs[element + "Val" + index];
        };
        getDom();
        while (dom) {
            dom.codemirror.setValue(trim(valDom.innerHTML));
            getDom();
        }
    });
  }, 
  methods:{
    runcode(){
      var m = this, frame = m.$refs.iframe;
      frame.src = "?a=" + Math.random() + "#/runcode"
      frame.onload = function() {
          frame.contentWindow.postMessage(m.$refs.myEditor1.codemirror.getValue(), "*");
      }

    }
  }
}
</script>  
<style scoped>
  .cy_example .codeArea{height:240px}
  .cy_example .iframeBox{flex:10}
</style>



